
import avatarSrc from '../userHeader/avator.png'
import Scroll from '../../../base/scroll/scroll'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import { mapActions } from 'vuex'
import { userInfoMixin } from '../../../assets/js/mixin'
export default {
name: "settings",
mixins:[userInfoMixin],
filters: {
isNull: function(value) {
if (value) {
return value
}
return '未填写'
}
},
components: {
Group,
Cell,
Scroll,
Actionsheet,
Datetime
},
created () {
this.avatorBox.avator = this.userInfo.headPicId || avatarSrc
this.userName.value = this.userInfo.username
this.userTaobao.value = this.userInfo.taobaoLevel
this.userSex.value = (this.userInfo.gender===0?'女':'男')
this.userAge.value = this.userInfo.birthday
this.userAddress.value = this.userInfo.postAddress
this.boxTwo[0].value = this.userInfo.qqNum
this.boxTwo[1].value = this.userInfo.telephone
},
data() {
return {
avatorBox: {
title: '头像',
link: '',
isLink: true
},
userName: {
title: '用户名',
link: '',
isLink: false,
},
userTaobao: {
title: '旺旺等级',
link: '',
isLink: true,
value: '二口夭',
info: '请及时更新真是的旺旺等级'
},
userSex: {
title: '性别',
link: 'settings/usersex',
isLink: true,
value: ''
},
userAge: {
title: '出生日期',
link: '',
isLink: true,
value: ''
},
userAddress: {
title: '收货地址',
link: 'settings/userAddress',
isLink: true,
value: ''
},
boxTwo: [
{
title: '联系QQ',
link: 'settings/userQq',
isLink: true,
value: '279332222',
info: '填写有助于提高中奖率'
},
{
title: '更改手机号',
link: 'settings/userPhone',
isLink: true,
value: '17745812564'
}
],
show: false,
menus2: {
menu1: '从相册选择',
menu2: '拍照'
},
visibility: false,
age: ''
}
},
watch: {
age(val) {

}
},
methods: {

isNull(value) {
if (value) {
return value
}
return '未填写'
},

avatorChange() {
this.show = true
},

ageChange() {
this.visibility = true
},

logout() {
const _this = this
this.$vux.confirm.show({
title: '确认退出登录吗',
content: '退出登录将不能查看个人信息',
onConfirm() {
_this.$axios.post('/api/user/loginOut', {}).then((response) => {
console.log(response)
_this.setUserInfo('')
_this.$router.push({ name: 'login' })
}).catch((error) => {
console.log(error)
})
}
})
},

updateUserInfo(updateObj) {
this.$axios.post('/api/user/update',updateObj).then((response) => {
}).catch((error) => {
console.log(error)
})
},
...mapActions([
'setUserInfo'
])
}
}



